<template>
	<div>
		<div class="header">
			<div class="search" @click="route('search')">
				<img src="../../static/img/icon/diary_icon/search@2x.png"/>
			</div>
			<div class="nav">
				<div v-for="(item, index) in navList" @tap="navIndex = index">
					<div  :class="{'active': index == navIndex}">{{item}}</div>
					<span :class="{'none': index ==2}">|</span>
				</div>
			</div>
			<div class="search">
				<img src="../../static/img/icon/find_icon/equipment@2x_46.png" @click="dialog = !dialog" v-if="navIndex != 2"/>
				<img class="shop" src="../../static/img/icon/find_icon/shoppingcart@3x.png" v-else @click="go('shoppingCart')"/>
			</div>
		</div>
		<div class="header-null"></div>
		
		<!--精选-->
		<selected v-if="navIndex == 0"></selected>
		
		<!--论坛-->
		<forum v-if="navIndex == 1"></forum>
		
		<div class="create-fixed" v-if="navIndex == 1" @click="route('createStatus')">
			<img src="../../static/img/icon/home_icon/riji_xuanzhong@2x.png"/>
		</div>
		<div class="create-fixed-left" @tap="couponShow" v-if="navIndex == 2">
			<img src="../../static/img/icon/find_icon/coupon.png"/>
		</div>
		
		<!--商城-->
		<mall v-if="navIndex == 2"></mall>
		
		<div class="mask">
			<div class="mask-bg" @tap="dialog = false" v-if="dialog"></div>
			<div class="content" v-if="dialog">
				<div>
					<div>应用设备</div>
					<div><img src="../../static/img/icon/find_icon/equipment@3x.png"/></div>
				</div>
				<div class="nav">
					<div>
						<div><img src="../../static/img/icon/find_icon/planter@3x.png"/></div>
						<div>智能种植机</div>
					</div>
					<div>
						<div><img src="../../static/img/icon/find_icon/plantlightinglamp@3x.png"/></div>
						<div>植物补光灯</div>
					</div>
				</div>
			</div>
		</div>
		
		<coupon title="优惠券专区" :id="couponId" :list="list" @receive="receiveCoupon" :dialog="show" @hide="hide" @pullup="pullup" @pulldown="pulldown"></coupon>
	</div>
</template>

<script>
	import mall from './mall'
	import coupon from '../base/couponMoreMask'
	import {getData} from '../service/getData'
	import selected from './selected'
	import forum from './forum'
	export default {
		data() {
			return {
				navList :['精选', '论坛', '商城'],
				navIndex: 0,
				navList1 : ['话题', '最新', '热门'],
				navList2: ['推荐','多肉达人','绿植养护','温室培养'],
				navIndex1: 1,
				navIndex2: 0,
				mallList: [],
				list: [],
				show: false,
				data: [],
				data1: [],
				data2: [],
				dialog: false,
				couponPage: 1,
				couponLastPage: 1,
				couponId: '#couponScroll'
			}
		},
		components: {
			mall,
			coupon,
			selected,
			forum
		},
		mounted() {
			if(sessionStorage.index) {
				this.navIndex = sessionStorage.index				
			}
		},
		methods: {
			hide() {
				this.show = false
			},
			pulldown() {
				getData('/coupons/getCouponsList', {
					pn: 1
				}).then(d => {
					mui(this.couponId).pullRefresh().endPulldownToRefresh(true);
					this.couponLastPage = d.pageInfo.lastPage
					this.list = d.pageInfo.list
				})
			},
			pullup() {
				if(this.couponLastPage != this.couponPage) {
					getData('/coupons/getCouponsList', {
						pn: this.couponPage++
					}).then(d => {
						mui(this.couponId).pullRefresh().endPullupToRefresh();
						this.list = this.list.concat(d.pageInfo.list)
					})					
				} else {
					mui(this.couponId).pullRefresh().endPullupToRefresh(true);
				}
			},
			receiveCoupon(obj) {
				getData('/usercoupons/getCouponsCountByUserId', {
					userId: sessionStorage.userId,
					couponsId: obj.CouponsID
				}).then(d => {
					if(d.code == 1) {
						if(d.userCoupons < obj.restrictCount) {
							getData('/usercoupons/collectionOfCoupons', {
								userId: sessionStorage.userId,
								couponsId: obj.CouponsID
							}).then(d => {
								mui.toast(d.msg);
							})													
						} else {
							mui.toast('领取上限');
						}
					}
				})
			},
			couponShow() {
				this.show = true
				getData('/coupons/getCouponsList', {
					pn: 1
				}).then(d => {
					this.list = d.pageInfo.list
					this.couponLastPage = d.pageInfo.lastPage
				})
			},
			go(str) {
				sessionStorage.index = 2
				this.route(str)
			},
		},
	}
</script>

<style scoped lang="less">

	.nav {
		display: flex;
		align-items: center;
		color: #000;
		
		div {
			display: flex;
			
			div {
				margin: 0 10px;
			}
			
			.active {
				color: #c9eb59;
				border-bottom: 1px solid #c9eb59;
			}		
			
			span {
				font-size: 12px;
			}
			
			.none {
				display: none;
			}
		}
	}
	
	.create-fixed {
		position: fixed;
		bottom: 10%;
		right: 20px;
		width: 40px;
		height: 40px;
		z-index: 3;
		img {
			width: 100%;
		}
	}
	
	.create-fixed-left {
		position: fixed;
		bottom: 8%;
		right: 20px;
		width: 50px;
		height: 50px;
		text-align: center;
		border-radius: 50%;
		background: #FFF;
		color: #c9eb59;
		/*text-shadow: 1px 1px 4px #000;*/
		box-shadow: 0 0 10px #c9eb59;
		padding: 10px;
		z-index: 3;
		display: flex;
		justify-content: center;
		align-items: center;
		
		img {
			width: 30px;
			height: 20px;
		}
	}
	
	.mask {
		
		
		.mask-bg {
			position: fixed;
			background: rgba(0,0,0,0.6);
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			z-index: 10;
		}
		
		.content {
			position: fixed;
			top: 0;
			width: 100%;
			height: 150px;
			background: #fff;
			padding: 60px 10px;
			z-index: 11;
			
			div {
				display: flex;
				align-items: center;
				margin-bottom: 5px;
				
				img {
					width: 20px;
					height: 12px;
					margin-left: 5px;
				}
				
			}

			.nav {
					
				div {
					background: #f7f4f4;
					border-radius: 10px;
					margin: 0;
					padding: 2px;
					margin-right: 10px;
					
					img {
						width: 25px;
						height: 25px;
					}
				}
			}
		}
	}
</style>


