<template>
	<div>
		<div class="content-nav-list">
			<div :class="{'content-nav-list-item': true, 'active': index == navIndex}" v-for="(item, index) in navList" @tap="navIndex = index">
				{{item}}
			</div>
		</div>
		<div class="mui-content mui-scroll-wrapper" id="pullrefresh2">
			<div class="mui-scroll">
				
				<div class="topic-list" v-if="navIndex == 0">
					<div class="topic-list-item" @tap="go('conversationDetail')" v-for="(item, index) in data">
						<img src="../../build/logo.png" width="100%" height="200" />
						<div class="num">
							<img src="../../static/img/icon/find_icon/xinxi.png" width="20" height="20" />
							<div>123</div>
						</div>
					</div>
				</div>
				<div class="content-list" v-if="navIndex == 1">
					<div class="content-list-follow">
						<div class="content-list-follow-header">
							<div>达人推荐</div>
							<div @tap="go('followMore')">
								<div>查看更多</div>
								<img src="../../static/img/icon/my_icon/back@3x.png" />
							</div>
						</div>
						<div class="content-list-follow-list">
							<div class="item" @tap="go('followDetail')" v-for="item in navList">
								<img src="../../static/img/icon/denglu_gengxin_icon/logo_denglu@3x.png" />
								<div>喜绿-推荐优选</div>
								<div class="btn">
									<img src="../../static/img/icon/find_icon/follow@3x.png" />
									<div>关注</div>
								</div>
							</div>
						</div>
					</div>
					<div class="content-list-item" v-for="(item, index) in data1">
						<div class="content-list-item-title">
							<div>
								<img src="../../static/img/icon/denglu_gengxin_icon/qq@3x.png" />
								<div class="item-user">
									<div class="item-user-title">
										{{item.user_name}}
									</div>
									<div class="item-user-date">
										{{item.h}}:{{item.m}}&nbsp;&nbsp;来自于<span style="color: #DD524D;">{{item.type}}</span>
									</div>
								</div>
							</div>
							<div>
								<div class="btn" v-if="item.isFollow == 0" @tap="follow(1, item)">关注</div>
								<div class="btn active" v-else @tap="cancel(item)">已关注</div>
							</div>
						</div>
						<div class="content-list-item-content" @tap="go('forumDetail', item.note_id)">
							<div class="item-content-title">{{item.note_name}}</div>
							<div class="item-content">
								{{item.note_content}}
							</div>
							<div class="item-content-imgs">
								<img src="../../static/img/icon/denglu_gengxin_icon/logo_denglu@2x.png" />
								<img src="../../static/img/icon/denglu_gengxin_icon/logo_denglu@2x.png" />
								<img src="../../static/img/icon/denglu_gengxin_icon/logo_denglu@2x.png" />
							</div>
							<div class="item-content-bottom">
								浏览{{item.look_count || 0}}次
							</div>
						</div>
						<div class="item-bottom">
							<div class="item-bottom-item">
								<div>
									<img src="../../static/img/icon/my_icon/heart_luntan@2x.png" v-if="" @tap="good(item)" />
									<!--<img src="../../static/img/icon/find_icon/heart_xuanzhong_luntan@3x.png"/>-->
									<div class="margin-left">{{item.like_count || '赞'}}</div>
								</div>
							</div>
							<div class="item-bottom-item">
								<div>
									<img src="../../static/img/icon/find_icon/pinglun_luntan@2x.png" />
									<div class="margin-left">{{item.comment_count || '评论'}}</div>
								</div>
							</div>
							<div class="item-bottom-item">
								<div>
									<img src="../../static/img/icon/find_icon/share_luntan@3x.png" />
									<!--<div class="margin-left">44</div>-->
								</div>
							</div>
						</div>
					</div>
				</div>
				<div class="content-list" v-if="navIndex == 2">
					<div class="content-list-item" v-for="(item,index) in data2">
						<div class="content-list-item-title">
							<div>
								<img src="../../static/img/icon/denglu_gengxin_icon/qq@3x.png" />
								<div class="item-user">
									<div class="item-user-title">
										{{item.user_name}}
									</div>
									<div class="item-user-date">
										{{item.h}}:{{item.m}}&nbsp;&nbsp;来自于<span style="color: #DD524D;">{{item.type}}</span>
									</div>
								</div>
							</div>
							<div>
								<div class="btn" v-if="item.isFollow == 0" @tap="follow(2, item)">关注</div>
								<div class="btn active" v-else @tap="cancel(item)">已关注</div>
							</div>
						</div>
						<div class="content-list-item-content" @tap="go('newestDetail', item.note_id)">
							<div class="item-content-title">{{item.note_name}}</div>
							<div class="item-content" v-html="item.note_content">
		
							</div>
							<div class="item-content-imgs">
								<img :src="i" v-for="(i, index) in item.img_path.split(';')" />
							</div>
							<div class="item-content-bottom">
								浏览{{item.look_count || 0}}次
							</div>
						</div>
						<div class="item-bottom">
							<div class="item-bottom-item">
								<div>
									<img src="../../static/img/icon/my_icon/heart_luntan@2x.png" />
									<div class="margin-left">{{item.like_count || '赞'}}</div>
								</div>
							</div>
							<div class="item-bottom-item">
								<div>
									<img src="../../static/img/icon/find_icon/pinglun_luntan@2x.png" />
									<div class="margin-left">{{item.comment_count || '评论'}}</div>
								</div>
							</div>
							<div class="item-bottom-item">
								<div>
									<img src="../../static/img/icon/find_icon/share_luntan@3x.png" />
									<div class="margin-left">{{item.look_count || '分享'}}</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { getData } from '../service/getData'
	export default {
		data() {
			return {
				data: [],
				data1: [],
				data2: [],
				navIndex: 1,
				navList: ['话题', '最新', '热门'],
				page: 1,
				lastPage: 1
			}
		},
		methods: {
			init() {
				this.$store.state.isLoading = true
				getData('/note/appGetNote', {
					page: 1,
					noteType: 2,
					findType: 1,
					chooseId: 0,
					topicId: 0,
					token: sessionStorage.token
				}).then(d => {
					this.$store.state.isLoading = false
					if(d.code == 100) {
						d.note.list.map(x => {
							if(x.note_type == 2) {
								x.type = '分享生活'
							} else if(x.note_type == 3) {
								x.type = '参与话题'
							} else if(x.note_type == 4) {
								x.type = '精选'
							} else if(x.note_type == 5) {
								x.type = '攻略'
							}
							let date = new Date(x.time)
							x.h = date.getHours()
							x.m = date.getMinutes()
							x.s = date.getSeconds()
						})
						this.data1 = d.note.list
						this.lastPage = d.note.lastPage
					}
				}).catch(err => {
					this.$store.state.isLoading = false
					console.log(err)
				})
			},
			good(item) {
				getData('/like/appToLike', {
					token: sessionStorage.token,
					pdId: item.note_id,
					noteType: item.note_type
				}).then(d => {
					if(d.code == 100) {
						mui.toast(d.msg)
					}
				})
			},
			go(str, id) {
				sessionStorage.index = 1
				this.route(str, id)
			},
			follow(status, item) {
				//				if(status == 1) {
				getData('/follow/appAddFollow', {
					token: sessionStorage.token,
					userId: item.userId
				}).then(d => {
					if(d.code == 100) {
						mui.toast(d.msg)
						item.isFollow = 1
					}
				})
				//				} else if(status == 2) {
				//					getData('/follow/appAddFollow', {
				//						token: sessionStorage.token,
				//						userId: item.id
				//					}).then(d => {
				//						item.isFollow = 1
				//					})
				//				}
			},
			cancel(item) {
				getData('/follow/appDeleteFollow', {
					token: sessionStorage.token,
					userId: item.userId
				}).then(d => {
					if(d.code == 100) {
						item.isFollow = 0
					}
				})
			},
			pullupRefresh() {
				if(this.lastPage != this.page) {
					if(this.navIndex == 1) {
						getData('/note/appGetNote', {
							page: this.page++,
							noteType: 2,
							findType: 1,
							chooseId: 0,
							topicId: 0,
							token: sessionStorage.token
						}).then(d => {
							if(d.code == 100) {
								d.note.list.map(x => {
									if(x.note_type == 2) {
										x.type = '分享生活'
									} else if(x.note_type == 3) {
										x.type = '参与话题'
									} else if(x.note_type == 4) {
										x.type = '精选'
									} else if(x.note_type == 5) {
										x.type = '攻略'
									}
									let date = new Date(x.time)
									x.h = date.getHours()
									x.m = date.getMinutes()
									x.s = date.getSeconds()
								})
								mui('#pullrefresh2').pullRefresh().endPullupToRefresh();
								this.data1 = this.data1.concat(d.note.list)
							}
						})
					} else if(this.navIndex == 0) {
						getData('/note/appGetNote', {
							token: sessionStorage.token,
							page: this.page++,
							noteType: 4,
							findType: 3,
							chooseId: 0,
							topicId: 0
						}).then(d => {
							if(d.code == 100) {
								mui('#pullrefresh2').pullRefresh().endPullupToRefresh();
								this.data = this.data.concat(d.note.list)
							}
						}).catch(err => {
							console.log(err)
						})
					} else if(this.navIndex == 2) {
						getData('/note/appGetNote', {
							page: this.page++,
							noteType: 3,
							findType: 2,
							chooseId: 0,
							topicId: 0,
							token: sessionStorage.token
						}).then(d => {
							if(d.code == 100) {
								d.note.list.map(x => {
									if(x.note_type == 2) {
										x.type = '分享生活'
									} else if(x.note_type == 3) {
										x.type = '参与话题'
									} else if(x.note_type == 4) {
										x.type = '精选'
									} else if(x.note_type == 5) {
										x.type = '攻略'
									}
									let date = new Date(x.time)
									x.h = date.getHours()
									x.m = date.getMinutes()
									x.s = date.getSeconds()
								})
								mui('#pullrefresh2').pullRefresh().endPullupToRefresh();
								this.data2 = this.data2.concat(d.note.list)
							}
						}).catch(err => {
							console.log(err)
						})
					}
				} else {
					mui('#pullrefresh2').pullRefresh().endPullupToRefresh(true);
				}
           	},
           	pulldownRefresh () {
           		if(this.navIndex == 1) {
					getData('/note/appGetNote', {
						page: 1,
						noteType: 2,
						findType: 1,
						chooseId: 0,
						topicId: 0,
						token: sessionStorage.token
					}).then(d => {
						mui('#pullrefresh2').pullRefresh().endPulldownToRefresh();
						if(d.code == 100) {
							d.note.list.map(x => {
								if(x.note_type == 2) {
									x.type = '分享生活'
								} else if(x.note_type == 3) {
									x.type = '参与话题'
								} else if(x.note_type == 4) {
									x.type = '精选'
								} else if(x.note_type == 5) {
									x.type = '攻略'
								}
								let date = new Date(x.time)
								x.h = date.getHours()
								x.m = date.getMinutes()
								x.s = date.getSeconds()
							})
							this.data2 = d.note.list
							this.lastPage = d.note.lastPage
						}
					})
				} else if(this.navIndex == 0) {
					getData('/note/appGetNote', {
						token: sessionStorage.token,
						page: 1,
						noteType: 4,
						findType: 3,
						chooseId: 0,
						topicId: 0
					}).then(d => {
						mui('#pullrefresh2').pullRefresh().endPulldownToRefresh();
						if(d.code == 100) {
							this.data2 = d.note.list
							this.lastPage = d.note.lastPage
						}
					}).catch(err => {
						console.log(err)
					})
				} else if(this.navIndex == 2) {
					getData('/note/appGetNote', {
						page: 1,
						noteType: 3,
						findType: 2,
						chooseId: 0,
						topicId: 0,
						token: sessionStorage.token
					}).then(d => {
						mui('#pullrefresh2').pullRefresh().endPulldownToRefresh();
						if(d.code == 100) {
							d.note.list.map(x => {
								if(x.note_type == 2) {
									x.type = '分享生活'
								} else if(x.note_type == 3) {
									x.type = '参与话题'
								} else if(x.note_type == 4) {
									x.type = '精选'
								} else if(x.note_type == 5) {
									x.type = '攻略'
								}
								let date = new Date(x.time)
								x.h = date.getHours()
								x.m = date.getMinutes()
								x.s = date.getSeconds()
							})
							this.data2 = d.note.list
							this.lastPage = d.note.lastPage
						}
					}).catch(err => {
						console.log(err)
					})
				}
           	}
		},
		mounted() {
			this.init()
			this.$nextTick(() => {
				mui.init({
	                pullRefresh: {
	                    container: '#pullrefresh2',
	                    up: {//上拉加载
	                    	height: 40,
	                        auto:false,//可选,默认false.自动上拉加载一次
	                        contentrefresh: '正在加载...',
	                        contentnomore:'没有更多数据了',//可选，请求完毕若没有更多数据时显示的提醒内容；
	                        callback: this.pullupRefresh
	                    },
						down: {                        
							contentdown: "下拉可以刷新", //可选，在下拉可刷新状态时，下拉刷新控件上显示的标题内容                        
							contentover: "释放立即刷新", //可选，在释放可刷新状态时，下拉刷新控件上显示的标题内容                        
							contentrefresh: "正在刷新…", //可选，正在刷新状态时，下拉刷新控件上显示的标题内容                        
							auto: false,//可选,默认false.首次加载自动下拉刷新一次                        
							callback: this.pulldownRefresh                   
						}
	                }
	            });
			})
		},
		created() {
			//处理mui下拉加载的bug
			for(var i = mui.hooks.inits.length-1,item;i>=0;i--){
				item=mui.hooks.inits[i];
				if(item.name=="pullrefresh"){
				    item.repeat=true;
				}
			}
		},
		watch: {
			navIndex(val) {
				this.page = 1
				this.$store.state.isLoading = true
				if(val == 1) {
					getData('/note/appGetNote', {
						page: 1,
						noteType: 2,
						findType: 1,
						chooseId: 0,
						topicId: 0,
						token: sessionStorage.token
					}).then(d => {
						this.$store.state.isLoading = false
						if(d.code == 100) {
							d.note.list.map(x => {
								if(x.note_type == 2) {
									x.type = '分享生活'
								} else if(x.note_type == 3) {
									x.type = '参与话题'
								} else if(x.note_type == 4) {
									x.type = '精选'
								} else if(x.note_type == 5) {
									x.type = '攻略'
								}
								let date = new Date(x.time)
								x.h = date.getHours()
								x.m = date.getMinutes()
								x.s = date.getSeconds()
							})
							this.data1 = d.note.list
							this.lastPage = d.note.lastPage
						}
					}).catch(err => {
						this.$store.state.isLoading = false
						console.log(err)
					})
				} else if(val == 0) {
					getData('/note/appGetNote', {
						token: sessionStorage.token,
						page: 1,
						noteType: 4,
						findType: 3,
						chooseId: 0,
						topicId: 0
					}).then(d => {
						this.$store.state.isLoading = false
						if(d.code == 100) {
							this.data = d.note.list
							this.lastPage = d.note.lastPage
						}
					}).catch(err => {
						this.$store.state.isLoading = false
						console.log(err)
					})
				} else if(val == 2) {
					getData('/note/appGetNote', {
						page: 1,
						noteType: 3,
						findType: 2,
						chooseId: 0,
						topicId: 0,
						token: sessionStorage.token
					}).then(d => {
						this.$store.state.isLoading = false
						if(d.code == 100) {
							d.note.list.map(x => {
								if(x.note_type == 2) {
									x.type = '分享生活'
								} else if(x.note_type == 3) {
									x.type = '参与话题'
								} else if(x.note_type == 4) {
									x.type = '精选'
								} else if(x.note_type == 5) {
									x.type = '攻略'
								}
								let date = new Date(x.time)
								x.h = date.getHours()
								x.m = date.getMinutes()
								x.s = date.getSeconds()
							})
							this.data2 = d.note.list
							this.lastPage = d.note.lastPage
						}
					}).catch(err => {
						this.$store.state.isLoading = false
						console.log(err)
					})
				}
			}
		}
	}
</script>

<style scoped lang="less">
	#pullrefresh2 {
		margin-top: 80px;
	}

	.content-nav-list {
		position: fixed;
		background: #eee;
		width: 100%;
		top: 50px;
		display: flex;
		.content-nav-list-item {
			flex: 1;
			text-align: center;
			padding: 5px 0;
		}
		.active {
			font-size: 15px;
			font-weight: bold;
		}
	}
	
	.topic-list {
		.topic-list-item {
			position: relative;
			margin-bottom: 10px;
			.num {
				position: absolute;
				top: 10px;
				right: 20px;
				display: flex;
				align-items: center;
				z-index: 1;
				div {
					color: #fff;
					margin-left: 5px;
				}
			}
		}
	}
	
	.content-list {
		
		.content-list-follow {
			background: #fff;
			border-bottom: 1px solid #eee;
			.content-list-follow-header {
				display: flex;
				justify-content: space-between;
				padding: 10px;
				div:nth-child(2) {
					color: #7B7B7B;
					display: -webkit-box;
					align-items: center;
					justify-content: center;
					img {
						width: 5px;
						height: 10px;
						margin-left: 5px;
					}
				}
			}
			.content-list-follow-list {
				padding: 0 10px 10px;
				display: flex;
				.item {
					width: 33.3%;
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: center;
					text-align: center;
					img {
						width: 70%;
					}
					.btn {
						padding: 2px 15px;
						display: flex;
						align-items: center;
						border-radius: 99px;
						background: #c9eb59;
						img {
							width: 8px;
							height: 8px;
							margin-right: 5px;
						}
					}
					.active {
						background: #ccc;
						color: #7B7B7B;
					}
				}
			}
		}
		.content-list-item {
			margin-bottom: 10px;
			padding: 15px 15px 0;
			background: #fff;
			.content-list-item-title {
				display: flex;
				align-items: center;
				justify-content: space-between;
				margin-bottom: 5px;
				div {
					display: flex;
					img {
						width: 50px;
						height: 50px;
						margin-right: 10px;
					}
					.item-user {
						flex-direction: column;
						display: flex;
						justify-content: center;
						.item-user-title {
							font-size: 15px;
							text-align: left;
						}
						.item-user-date {
							color: #7B7B7B;
							width: 100%;
						}
					}
					.btn {
						display: table;
						align-items: center;
						padding: 3px 15px;
						border-radius: 5px;
						background: #c9eb59;
						color: #fff;
					}
					.active {
						background: #ccc;
						color: #7B7B7B;
					}
				}
			}
			.content-list-item-content {
				border-bottom: 1px solid #e5e5e5;
				.item-content-title {
					font-size: 15px;
					font-weight: bold;
				}
				.item-content-imgs {
					display: -webkit-box;
					img {
						width: 22.5%;
						/*flex: 1;*/
						margin-right: 5px;
						&:last-child {
							margin: 0;
						}
					}
				}
				.item-content-bottom {
					color: #7B7B7B;
					margin: 5px 0;
				}
			}
			.item-bottom {
				display: flex;
				.item-bottom-item {
					flex: 1;
					align-items: center;
					display: flex;
					padding: 10px 10px;
					div {
						display: flex;
						margin: 0 auto;
						.margin-left {
							margin-left: 10px;
						}
					}
					img {
						width: 20px;
						height: 18px;
					}
				}
			}
		}
	}
</style>


