<template>
	<div id="index">
		<div class="mui-content mui-scroll-wrapper" id="pullrefresh1">
			<div class="mui-scroll">
				<div class="mui-slider" id="slider">
				  <div class="mui-slider-group mui-slider-loop">
				    <!--第一个内容区容器-->
				    <div class="mui-slider-item mui-slider-item-duplicate" @tap="go('commodityDetail',lastImg.sortId)">
				      <img :src="lastImg.sortPicture"/>
				    </div>
				    <div class="mui-slider-item" v-for="item in list" @tap="go('commodityDetail', item.sortId)">
				      <img :src="item.sortPicture"/>
				    </div>
				    <div class="mui-slider-item mui-slider-item-duplicate" @tap="go('commodityDetail',firstImg.sortId)">
				      <img :src="firstImg.sortPicture"/>
				    </div>
				  </div>
				  <div class="mui-slider-indicator">
				    <div :class="{'mui-indicator': true,  'mui-active': index == 0}" v-for="(item, index) in list"></div>
				  </div>
				</div>
				<div class="content1">
					<div class="content1-tab">
						<div class="content1-tab-item" @tap="tab('all')">
							<div :class="{'active-text': tabIndex == 'all'}">全部</div>
							<div :class="{'active': tabIndex == 'all'}"></div>
						</div>
						<div class="content1-tab-item" v-for="(item, index) in typeList" v-show="index < 3"  @tap="tab(item.CommodityClassifyID)">
							<div :class="{'active-text': tabIndex == item.CommodityClassifyID}">{{item.classifyName}}</div>
							<div :class="{'active': tabIndex == item.CommodityClassifyID}"></div>
						</div>
						<div class="content1-tab-item" @tap="typeMore = true">
							...
						</div>
					</div>
					<div class="commodity-tab">
						<div class="commodity-tab-item" v-for="(item, index) in index" @tap="index == 0? navClick(index) : tabItem(index)">
							<div :class="{'commodity-tab-item-name': true, 'active':index==tabId}" :data-id="index">
								<span>{{item}}</span>
								<span v-if="index == 0">
									<img v-if="!tabId == 1" :class="{'up': dialog == true}" src="../../static/img/icon/find_icon/xia.png"/>
									<img v-else src="../../static/img/icon/find_icon/xia-1.png"/>
								</span>
							</div>
						</div>
					</div>
					<div class="commodity-content">
						<div class="commodity-content-list" @tap="go('commodityDetail',item.CommodityID)" v-for="(item, index) in data">
							<div class="commodity-content-list-left">
								<img :src="item.titleUrl.split(',')[0]"/>
							</div>
							<div class="commodity-content-list-right">
								<div class="commodity-content-list-title">
									{{item.commodityName}}
								</div>
								<div class="commodity-content-list-bottom">
									<div class="commodity-content-list-money">
										￥{{item.commodityPrice}} <span>￥{{item.discountPrice}}</span>
									</div>
									<div class="commodity-content-list-bottom-right">
										已售{{item.salesVolume == null ? 0 : item.salesVolume}}
									</div>
								</div>
							</div>
						</div>
					</div>
					<div class="dialog" v-if="dialog">
						<div class="item" v-for="(item, index) in screenList" @tap="dialogClick(index)">
							<div>{{item}}</div>
							<div v-if="index == dialogIndex"><img src="../../static/img/icon/find_icon/gou.png"/></div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<coupon-mask :dialog="show" @hide="hide"></coupon-mask>
		<more-type :dialog="typeMore" :list="typeList" @hide="hide" @success="success"></more-type>
	</div>
</template>

<script>
	import couponMask from '../base/couponMask'
	import {getData} from '../service/getData'
	import moreType from '../base/drag-right'
	export default {
		components: {
			couponMask,
			moreType
		},
		data() {
			return {
				show: false,
				firstImg: '',
				lastImg: '',
				typeList: [],
				data: [],
				tabIndex: 'all',
				type: {},
				type1: {},
				type2: {},
				typeMore: false,
				index:['综合','销量','最新上架'],
				tabId: 0,
				dialog: false,
				screenList: ['综合','价格升序','价格降序'],
				dialogIndex: 0,
				list: [],
				page: 1,
				lastPage: 1,
			}
		},
		methods: {
			hide() {
				this.show = false
				this.typeMore = false
			},
			init() {
				this.$store.state.isLoading = true
				getData('/commodityclassify/getClassifyByInfoApi').then(d => {
					this.typeList = d.classifyList
				})
				
				getData('/mallhomepagesort/getAllHomePageApi').then(d => {
					this.list = d.pageSortList
				})
				
				getData('/commodity/getCommodityListByInfoApi', {
					pn: 1
				}).then(d => {
					this.$store.state.isLoading = false
					this.data = d.pageInfo.list
					this.lastPage = d.pageInfo.lastPage
				}).catch(err => {
					this.$store.state.isLoading = false
					console.log(err)
				})
			},
			dialogClick(index) {
				this.dialogIndex = index
				this.dialog = false
				if(index == 0) {
					if(typeof this.tabIndex == 'number') {			
						getData('/commodity/getCommodityListByInfoApi', {
							pn: 1,
							parentClassify: this.tabIndex,
							orderByType: 1
						}).then(d => {
							this.data = d.pageInfo.list
						})
					} else {
						getData('/commodity/getCommodityListByInfoApi', {
							pn: 1,
							orderByType: 1
						}).then(d => {
							this.data = d.pageInfo.list
						})
					}
				} else {
					if(typeof this.tabIndex == 'number') {
						getData('/commodity/getCommodityListByInfoApi', {
							pn: 1,
							parentClassify: this.tabIndex,
							sortType: index == 1? 2: 1
						}).then(d => {
							this.data = d.pageInfo.list
						})											
					} else {
						getData('/commodity/getCommodityListByInfoApi', {
							pn: 1,
							sortType: index == 1? 2: 1
						}).then(d => {
							this.data = d.pageInfo.list
						})	
					}
				}
			},
			success(id) {
				this.tabIndex = id
				getData('/commodity/getCommodityListByInfoApi', {
					pn: 1,
					parentClassify: id,
					orderByType: this.tabId + 1
				}).then(d => {
					this.data = d.pageInfo.list
				})
			},
			navClick(index) {
				this.dialog = !this.dialog
				this.tabId = index
			},
			tabItem(index) {
				this.tabId = index
				this.dialog = false
				if(index == 0) {
					if(this.dialogIndex == 0) {
						getData('/commodity/getCommodityListByInfoApi', {
							pn: 1,
							parentClassify: this.tabIndex == 'all' ? 1 : this.tabIndex + 1,
							orderByType: 1
						}).then(d => {
							this.data = d.pageInfo.list
						})
					} else {
						getData('/commodity/getCommodityListByInfoApi', {
							pn: 1,
							parentClassify: this.tabIndex == 'all' ? 1 : this.tabIndex + 1,
							sortType: this.dialogIndex == 1? 2: 1
						}).then(d => {
							this.data = d.pageInfo.list
						})
					}
				} else {
					getData('/commodity/getCommodityListByInfoApi', {
						pn: 1,
						parentClassify: this.tabIndex == 'all' ? 1 : this.tabIndex + 1,
						orderByType: index + 1
					}).then(d => {
						this.data = d.pageInfo.list
					})
				}
			},
			tab(str) {
				this.dialog = false
				if(typeof str == 'number') {
					this.tabIndex = str
					getData('/commodity/getCommodityListByInfoApi', {
						pn: 1,
						parentClassify: str,
						orderByType: this.tabId + 1
					}).then(d => {
						this.data = d.pageInfo.list
					})
				} else if(str == 'all') {
					this.tabIndex = str
					getData('/commodity/getCommodityListByInfoApi', {
						pn: 1
					}).then(d => {
						this.data = d.pageInfo.list
					})
				} else {
					this.typeMore = true
				}
			},
			go(str, id) {
				sessionStorage.index = 2
				this.route(str, id)
			},
			pullupRefresh: function() {
				if(this.lastPage != this.page) {
					getData('/commodity/getCommodityListByInfoApi', {
						pn: this.page++
					}).then(d => {
						mui('#pullrefresh1').pullRefresh().endPullupToRefresh();
						this.data = this.data.concat(d.pageInfo.list)
					})
				} else {
					mui('#pullrefresh1').pullRefresh().endPullupToRefresh(true);
				}
           	},
           	pulldownRefresh () {
           		getData('/commodity/getCommodityListByInfoApi', {
					pn: 1
				}).then(d => {
					mui('#pullrefresh1').pullRefresh().endPulldownToRefresh();
					this.$store.state.isLoading = false
					this.data = d.pageInfo.list
					this.lastPage = d.pageInfo.lastPage
				}).catch(err => {
					this.$store.state.isLoading = false
					console.log(err)
				})
           	}
		},
		mounted() {
			this.$nextTick(() => {
			    //获得slider插件对象
//					    mui(".mui-slider").slider();
			    var slider = mui(".mui-slider");
				slider.slider({
					interval: 5000
				});
				
				//mui上拉加载
				mui.init({
	                pullRefresh: {
	                    container: '#pullrefresh1',
	                    up: {//上拉加载
	                    	height: 40,
	                        auto:false,//可选,默认false.自动上拉加载一次
	                        contentrefresh: '正在加载...',
	                        contentnomore:'没有更多数据了',//可选，请求完毕若没有更多数据时显示的提醒内容；
	                        callback: this.pullupRefresh
	                    },
						down: {                        
							contentdown: "下拉可以刷新", //可选，在下拉可刷新状态时，下拉刷新控件上显示的标题内容                        
							contentover: "释放立即刷新", //可选，在释放可刷新状态时，下拉刷新控件上显示的标题内容                        
							contentrefresh: "正在刷新…", //可选，正在刷新状态时，下拉刷新控件上显示的标题内容                        
							auto: false,//可选,默认false.首次加载自动下拉刷新一次                        
							callback: this.pulldownRefresh                   
						}
	                }
	            });
			})
			this.init()
		},
		created() {
			//处理mui下拉加载的bug
			for(var i = mui.hooks.inits.length-1,item;i>=0;i--){
				item=mui.hooks.inits[i];
				if(item.name=="pullrefresh"){
				    item.repeat=true;
				}
			}
		},
		watch: {
			list(val) {
				if(val.length != 0) {
					this.firstImg = val[0]
					this.lastImg = val[val.length-1]
				}
			}
		}
	}
</script>

<style scoped lang="less">
	#index {
		background: #eee;
	}
	
	#pullrefresh1 {
		margin-top: 50px;
	}
	
	.content {
		background: #fff;
	}
	
	.content-imgs {
		width: 50%;
		display: flex;
		flex-wrap: wrap;
		padding-top: 20px;
		padding-bottom: 10px;
		
		.content-imgs-item {
			flex: 1;
			width: 48%;
			height: 50%;
			background: #eee;
			
			&:nth-child(1) {
				margin-bottom: 2%;	
			}
			&:nth-child(2) {
				margin-left: 2%;
				margin-bottom: 2%;
			}
			&:nth-child(4) {
				margin-left: 2%;
			}
		}
	}
	
	.content1 {
		/*border-top: 16px solid #fff;*/
		position: relative;
		.content1-title {
			position: absolute;
			top: -1rem;
			padding: 5px 20px;
			background: coral;
			border-radius: 0 99px 99px 0;
			font-size: 14px;
			color: #fff;
		}
		
		.dialog {
			position: absolute;
			top: 82px;
			width: 100%;
			background: #fff;
			z-index: 2;
			padding: 0 10px;
			
			.item {
				border-bottom: 1px solid #e5e5e5;
				padding: 10px 0;
				display: flex;
				justify-content: space-between;
				
				img {
					width: 20px;
					height: 12px;
				}
			}
		}
		
		.content1-tab {
			display: flex;
			background: #fff;
			overflow-x: scroll;
			/*flex-wrap: wrap;*/
			align-items: center;
			padding: 15px 0;
			margin-bottom: 1px;
			
			.content1-tab-item {
				align-items: center;
				justify-content: center;
				flex: 1;
				display: flex;
				height: 12px;
				flex-direction: column;
				border-right: 1px solid #7B7B7B;
				
				&:last-child {
					border: 0;
				}
			}
			
			.active-text {
				color: #c9eb59;
			}
			
			.active {
				width: 20%;
				border-bottom: 2px solid #c9eb59;
				color: #c9eb59;
			}
		}
		
		.commodity-tab {
			width: 100%;
			background: #fff;
			border-bottom: 1px solid #e5e5e5;
			display: flex;
			font-size: 14px;
			
			.commodity-tab-item {
				flex: 1;
				padding: 10px 0 0;
				text-align: center;
				
				.commodity-tab-item-name {
					display: inline-block;
					margin: 0 auto;
					padding: 0 5px 5px;
					
					img {
						width: 8px;
						height: 8px;
					}
					
					.up {
						transform: rotate(180deg);
					}
				}
			}
			
			.active {
				color: #c9eb59;
				font-weight: bold;
				/*border-bottom: 2px solid #c9eb59;*/
			}
		}
		
		.commodity-content {
			padding-left: 20px;
			background: #fff;
			
			.commodity-content-list {
				padding: 10px 10px 10px 0;
				display: flex;
				border-bottom: 1px solid #eee;
				
				.commodity-content-list-left {
					flex: 1;
					min-height: 80px;
					
					img {
						width: 100%;
						height: 100%;
						background: #eee;
					}
				}
				
				.commodity-content-list-right {
					flex: 3;
					font-size: 13px;
					margin-left: 10px;
					position: relative;
					
					.commodity-content-list-title {
						padding-right: 10px;
						display: -webkit-box;
						word-break: break-all;
						text-overflow: ellipsis;
						font-size: 14px;
						overflow: hidden;
						-webkit-box-orient: vertical;
						-webkit-line-clamp:2;	
					}
					
					.commodity-content-list-bottom {
						position: absolute;
						bottom: 2px;
						display: flex;
						width: 100%;
						justify-content: space-between;
						
						.commodity-content-list-money {
							color: red;
							font-weight: bold;
							
							span {
								font-weight: initial;
								color: #7b7b7b;
								font-size: 12px;
								text-decoration: line-through;
							}
						}
						
						.commodity-content-list-bottom-right {
							font-size: 12px;
							color: #7b7b7b;
						}
					}
				}
			}
		}
		
		.content1-list {
			width: 100%;
			display: flex;
			
			img {
				width: 100%;
				max-height: 200px;
			}
				
			img:nth-child(1) {
				flex: 5;
			}
			
			img:nth-child(2) {
				flex: 1;
			}
		}
	}
</style>


